<template>
  <div class="content_no">
    <i class="iconfont i1" @click="$router.back()">&#xe647;</i>
    <div class="d2">
      {{ data.name }}
    </div>
    <div class="d3">
      <i class="iconfont">&#xe601;</i>
      <span>{{ data.address }} </span>
      <i class="iconfont">&#xe643;</i>
    </div>
    <div class="d4">
      <i class="iconfont">&#xe953;</i>
      <span>{{ data.phone }}</span>
      <i class="iconfont">&#xe643;</i>
    </div>
    <ul v-if="data.services">
      <li v-for="value in data.services">
        <div>{{ value.name }}</div>
        <div>{{ value.description }}</div>
      </li>
    </ul>
  </div>
</template>

<script>
  import axios from "axios";
  import {Toast} from "vant";

  export default {
    data() {
      return {
        data: ""
      }
    },
    methods: {},
    mounted() {
      Toast.loading({
        message: 'loading...',
        forbidClick: true,
        duration: 0
      });
      axios({
        url: `https://m.maizuo.com/gateway/?cinemaId=${this.$route.params.id}&k=8830709`,
        headers: {
          'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16357749354350673021829121","bc":"370200"}',
          'X-Host': 'mall.film-ticket.cinema.info'
        }
      }).then(value => {
        this.data = value.data.data.cinema
        Toast.clear()
      })
    }
  }
</script>
<style scoped lang="scss">
  .content_no {
    width: 100%;
    background: #fff;
    position: relative;
    padding: 44px 15px;
    box-sizing: border-box;

    .i1 {
      position: absolute;
      top: 0;
      left: 10px;
      height: 44px;
      display: flex;
      align-items: center;
      font-size: 20px;
    }


    .d2 {
      text-align: center;
      height: 25px;
      margin-top: 10px;
      font-size: 17px;
    }

    .d3 {
      margin-top: 25px;
    }

    .d4 {
      border-bottom: 1px solid #ededed;
    }

    .d3, .d4 {
      padding: 15px 0;
      font-size: 14px;
      display: flex;
      border-top: 1px solid #ededed;

      span {
        margin: 0 26px 0 9px;
        width: 288px;
      }

      i:nth-of-type(2) {
        display: flex;
        align-items: center;
        transform: scale(.7);
        font-weight: 900;
      }
    }

    ul {
      margin-top: 25px;

      li {
        margin-bottom: 25px;
        display: flex;
        justify-content: space-between;


        div:nth-of-type(1) {
          box-sizing: border-box;
          max-width: 62px;
          line-height: 15px;
          padding: 0 6px;
          color: #ffb232;
          border: 1px solid #ffb232;
          border-radius: 1px;
          font-size: 10px;
          height: 17px;
        }

        div:nth-of-type(2) {
          width: calc(100% - 100px);
          font-size: 11px;
          color: #797d82;
        }
      }
    }
  }
</style>
